@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Ubuntu", serif;
}

.hide {
  display: none;
}
.show {
  display: flex !important;
}

/* Overview */

#overview {
  width: 720px;
  margin: 50px 0px 0px 80px;
  border-left: 1px solid rgba(0, 0, 0, 0.2);
  padding-left: 10px;
  max-width: 85%;
}

#cv-overview {
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
}

.overview-item {
  margin-left: 25px;
  margin-top: 10px;
}

#edit-cv {
  background: url("/cv-application/static/media/Edit_icon_(the_Noun_Project_30184).398bf333.svg");
  background-repeat: no-repeat;
  height: 100px;
  width: 100px;
  -webkit-transform: scale(0.3);
          transform: scale(0.3);
  position: absolute;
  top: 12px;
  left: 12px;
  cursor: pointer;
}

#cv-form {
  width: 720px;
  max-width: 90%;
  margin: auto;
  position: relative;
}

.cv-form-list {
  position: absolute;
  width: 720px;
  max-width: 90%;
  top: 0;
  display: flex;
  left: 5%;
  right: 5%;
  flex-direction: column;
  grid-gap: 15px;
  gap: 15px;
  margin-top: 100px;
  margin-bottom: 20px;
  display: none;
}

li {
  display: flex;
  flex-direction: column;
}

hr {
  -webkit-transform: translateY(-3px);
          transform: translateY(-3px);
  background: linear-gradient(
    to right,
    green 33.3%,
    white 33.3%,
    white 33.6%,
    red 33.6%,
    red 66.6%,
    white 66.6%,
    white 67%,
    red 67%
  );
  height: 12px;
  border: none;
}

#edu-info > hr {
  background: linear-gradient(
    to right,
    green 33.3%,
    white 33.3%,
    white 33.6%,
    green 33.6%,
    green 66.6%,
    white 66.6%,
    white 67%,
    red 67%
  );
}
#exp-info > hr {
  background: linear-gradient(
    to right,
    green 33.3%,
    white 33.3%,
    white 33.6%,
    green 33.6%,
    green 66.6%,
    white 66.6%,
    white 67%,
    green 67%
  );
}

h3 {
  margin-bottom: 3px;
  -webkit-transform: translateY(15px);
          transform: translateY(15px);
}

label {
  margin-bottom: 3px;
  cursor: pointer;
}

input {
  outline: none;
  padding: 3px;
  font-size: 18px;
}

#submit-form,
#next,
#back {
  padding: 7px 12px 5px 12px;
  font-size: 18px;
  margin: 0px auto 0px auto;
  display: flex;
  align-self: center;
  background: limegreen;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  transition-duration: 400ms;
  font-weight: bold;
  text-shadow: -1px -1px 1px gray;
}

#nav-btns {
  display: flex;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
  grid-gap: 3px;
  gap: 3px;
}

#submit-form:hover {
  background-color: lime;
}

#next {
  color: white;
  background-color: yellowgreen;
}
#back {
  background-color: tomato;
  text-shadow: -1px -1px 2px lightgray;
}

textarea {
  padding: 3px;
  font-size: 16px;
}

